import * as React from 'react';
import { Tag } from "antd";

interface IRecommendTypeBoxProps {
    type: number
}

export class RecommendTypeBox extends React.Component<IRecommendTypeBoxProps> {

    public render() {
        const Boxes = [{
            color: "#2196f3",
            content: "推荐"
        }, {
            color: "#ff5516",
            content: "优选活动"
        }]
        return (
            <div>
                <Tag color={Boxes[this.props.type].color}>{Boxes[this.props.type].content}</Tag>
            </div>
        );
    }
}
