import * as React from 'react';
import { Tag } from "antd";

interface ICityTypeBoxProps {
    type: number
}

export class CityTypeBox extends React.Component<ICityTypeBoxProps> {

    public render() {
        const Boxes = [{
            color: "#2196f3",
            content: "扬州"
        }, {
            color: "#ff5516",
            content: "蚌埠"
        }]
        return (
            <div>
                <Tag color={Boxes[this.props.type].color}>{Boxes[this.props.type].content}</Tag>
            </div>
        );
    }
}
