import * as React from 'react';
import { useEffect ,useState} from 'react'
import {Table ,message} from 'antd';
import { apiGetRecommendRelateTypeList } from '../api';

interface IRelateContentProps {
    /**
     * type 1 宠物 2 商品 3 服务
     */
    type: number
    userId: string
    selectRelateIdCallback?:(id:string)=>void
}

const RelateContent = (props: IRelateContentProps) => {

    const [tableData ,setTableData] = useState([])

    const [selectedRowKeys ,setSelectedRowKeys] = useState([])

    // const [currentPage] = useState(0)

    useEffect(() => {
        apiGetRecommendRelateTypeList(props.type,props.userId)
        .then((res:any)=>{
            if (res && res.data.length > 0) {
                const Data = res.data.map((item: any, index: number) => {
                    return {
                        key: item.id,  // key 应带对应data id 
                        title:item.title,
                        content:item.content
                    }
                })
                setTableData(Data)
            }else{
                setTableData([])
            }
        })
    }, [props.userId,props.type])

    const columns = [{
        title: '标题',
        dataIndex: 'title',
        key: 'title',
    },{
        title: '内容',
        dataIndex: 'content',
        key: 'content',
    }]

    const onSelectChange = (rowKeys:any)=>{
        if(props.selectRelateIdCallback){
            if(rowKeys.length>1){
                message.warn("只能推荐一条记录")
                return
            }
            props.selectRelateIdCallback(rowKeys[0])
        }
        setSelectedRowKeys(rowKeys);
    }

    const rowSelection = {
        selectedRowKeys,
        onChange: onSelectChange
    };
    return (
        <Table 
            rowSelection={rowSelection} columns={columns} 
            dataSource={tableData}
            // onChange={this.handleChange}
            locale={{ filterConfirm: "确定", filterReset: "重置", emptyText: "暂无数据" }}
        />
    )
}

export default RelateContent