import * as React from 'react';
import { useEffect, useState } from 'react'
import { Breadcrumb, Table, Drawer, Button, Radio, Input, DatePicker, message, Modal } from 'antd'
import { apiGetRecommendList, apiAddRecommend, apiEditRecommend, apiDeleteRecommend } from '../api';
import { RecommendTypeBox } from '../../components/RecommendTypeBox';
import { RecommendRelateTypeBox } from '../../components/RecommendTypeBox/relate';
import moment from 'moment';
import { CityTypeBox } from '../../components/RecommendTypeBox/city';
import { AdvertisementDetail } from '../advertisement/detail';
import locale from 'antd/lib/date-picker/locale/zh_CN';
import 'moment/locale/zh-cn';
import UserList from '../user/userlist';
import RelateContent from './relateContent';
moment.locale('zh-cn');
const RadioGroup = Radio.Group;

const { RangePicker } = DatePicker;

const Recommend = (props: any) => {

    const [show, setShow] = useState(false) // 详情

    const [tableData, setTableData] = useState([])

    const [total, setTotal] = useState(0)

    const [currentPage] = useState(0)

    const [drawerShow, setDrawerShow] = useState(false)

    const [recommendAddDrawerShow, setRecommendAddDrawerShow] = useState(false)
    const [relateId, setRelateId] = useState("")

    const [relateType, setRelateType] = useState(1)

    const [selectedRowKeys, setSelectedRowKeys] = useState([])

    const [recommendType, setRecommendType] = useState(1)

    const [recommendRelateType, setRecommendRelateType] = useState(1)

    const [name, setName] = useState("")

    const [cityCode, setCityCode] = useState("321000")

    const [startTime, setStartTime] = useState("")

    const [endTime, setEndTime] = useState("")

    const [edit, setedit] = useState(false)

    const [editId, setEditId] = useState()

    const [userTableShow, setUserTableShow] = useState(false)

    const [selectedUserId, setSelectedUserId] = useState("")

    const [selectedRelateId , setSelectedRelateId] = useState("")

    const [userRelateTableShow,setUserRelateTableShow] = useState(false)

    const clearData = ()=>{
        setRecommendAddDrawerShow(false)
        setedit(false)
        setEditId("")
        setUserRelateTableShow(false)
        setSelectedRelateId("")
        setSelectedUserId("")
        setEndTime("")
        setStartTime("")
        setName("")
    }

    const getRecommendList = (page: number, size: number) => {
        apiGetRecommendList(page, size).then((res: any) => {
            if (res && res.data.length > 0) {
                const Data = res.data.map((item: any, index: number) => {
                    return {
                        key: item.id,  // key 应带对应data id 
                        type: item.type ? item.type : 1,
                        relateType: item.relateType ? item.relateType : 1,
                        relateId: item.relateId,
                        name: item.name,
                        startTime: item.startTime,
                        endTime: item.endTime,
                        cityCode: item.cityCode,
                        deviceType: item.deviceType,
                        deviceName: item.deviceName,
                        userId: item.userId.id,
                        userName: item.userId.username,
                        cuserId: item.cuserId ? item.cuserId.name : "----",
                        muserId: item.muserId ? item.muserId.name : "----",
                        ct: item.ct,
                        mt: item.mt
                    }
                })
                setTableData(Data)
                setTotal(res.total)
            }
        })
    }

    useEffect(() => {
        const Page = props.match.params.page
        getRecommendList(Page, 10)
    }, [])

    const handleChange = (pagination: any, filters: any, sorter: any) => {
        // console.log('Various parameters', pagination, filters, sorter);
        props.history.push(`/home/inform/${pagination.current}`)
        getRecommendList(pagination.current, 10)
    }

    const handleClick = (Type: number, Id: string) => {
        // console.log(relateType,relateId)
        setShow(true)
        setRelateType(Type)
        setRelateId(Id)
    }

    const columns = [
        {
            title: '推荐类型',
            dataIndex: 'type',
            key: 'type',
            render: (text: any, record: any) => {
                return (
                    <RecommendTypeBox type={record.type - 1} />
                )
            }
        }, {
            title: '推荐关联',
            dataIndex: 'relateType',
            key: 'relateType',
            render: (text: any, record: any) => {
                return (
                    <RecommendRelateTypeBox type={record.relateType - 1} />
                )
            }
        }, {
            title: '关联ID',
            dataIndex: 'relateId',
            key: 'relateId',
            render: (text: any, record: any) => {
                return (
                    <a onClick={() => handleClick(record.relateType, record.relateId)}>关联详情</a>
                )
            },
        }, {
            title: '推荐开始时间',
            dataIndex: 'startTime',
            key: 'startTime',
            render: (text: any, record: any) => {
                return (
                    <span>{moment(parseInt(record.startTime, 10)).format('YYYY-MM-DD H:mm:ss')}</span>
                )
            },
            sorter: (a: any, b: any) => parseInt(a.startTime, 10) - parseInt(b.startTime, 10),
        }, {
            title: '推荐结束时间',
            dataIndex: 'endTime',
            key: 'endTime',
            render: (text: any, record: any) => {
                return (
                    <span>{moment(parseInt(record.endTime, 10)).format('YYYY-MM-DD H:mm:ss')}</span>
                )
            },
            sorter: (a: any, b: any) => parseInt(a.endTime, 10) - parseInt(b.endTime, 10),
        }, {
            title: '城市',
            dataIndex: 'cityCode',
            key: 'cityCode',
            render: (text: any, record: any) => {
                const CityIndex = record.cityCode === "321000" ? 1 : 2
                return (
                    <CityTypeBox type={CityIndex - 1} />
                )
            }
        }, {
            title: '发布人',
            dataIndex: 'userName',
            key: 'userName',
        }, {
            title: '创建者',
            dataIndex: 'cuserId',
            key: 'cuserId',
        }, {
            title: '最后修改者',
            dataIndex: 'muserId',
            key: 'muserId',
        }, {
            title: '创建时间',
            dataIndex: 'ct',
            key: 'ct',
            render: (text: any, record: any) => {
                return (
                    <span>{record.ct ? moment(parseInt(record.ct, 10)).format('YYYY-MM-DD H:mm:ss') : "----"}</span>
                )
            },
            sorter: (a: any, b: any) => parseInt(a.ct, 10) - parseInt(b.ct, 10),
        }, {
            title: '修改时间',
            dataIndex: 'mt',
            key: 'mt',
            render: (text: any, record: any) => {
                return (
                    <span>{record.mt ? moment(parseInt(record.mt, 10)).format('YYYY-MM-DD H:mm:ss') : "----"}</span>
                )
            },
            sorter: (a: any, b: any) => parseInt(a.mt, 10) - parseInt(b.mt, 10),
        }, {
            title: "操作",
            dataIndex: 'operation',
            key: 'operation',
            render: (text: any, record: any) => {
                return <a onClick={() => {
                    setedit(true)
                    setRecommendType(record.type)
                    setRecommendRelateType(record.relateType)
                    setRecommendAddDrawerShow(true)
                    setEditId(record.key)
                    setName(record.name)
                    setSelectedUserId(record.userId)
                    setSelectedRelateId(record.relateId)
                    setCityCode(record.cityCode)
                    
                    setStartTime(record.startTime)
                    
                    setEndTime(record.endTime)

                }}>编辑</a>
            }
        }
    ]

    const onSelectChange = (rowKeys: any) => {
        setSelectedRowKeys(rowKeys);
    }

    const rowSelection = {
        selectedRowKeys,
        onChange: onSelectChange
    };


    const addRecommend = () => {
        setRecommendAddDrawerShow(true)
        setStartTime(new Date().getTime().toString())
        setEndTime(new Date().getTime().toString())
    }

    const deleteRecommend = () =>{
        
        apiDeleteRecommend(selectedRowKeys).then((res: any) => {
            if (res) {
                message.success("删除成功")
                setTimeout(() => {
                    window.location.reload()
                }, 500);
            } else {
                message.error("删除失败")
            }
        })
    }

    const onChange = (date: any, dateString: string[]) => {

        const start = new Date(dateString[0])

        const end = new Date(dateString[1])

        setStartTime(start.getTime().toString())

        setEndTime(end.getTime().toString())
    }

    const submitAddRecommend = (type: number, id: number) => {

        if (type === 1) {
            apiAddRecommend(
                recommendType,
                recommendRelateType,
                selectedRelateId,
                name,
                startTime,
                endTime,
                cityCode,
                selectedUserId
            ).then((res: any) => {
                if (res.success) {
                    message.success(res.msg)
                    setTimeout(() => {
                        window.location.reload()
                    }, 500);
                } else {
                    message.error(res.msg)
                }
            })
        } else if (type === 2) {
            apiEditRecommend(
                editId,
                recommendType,
                recommendRelateType,
                selectedRelateId,
                name,
                startTime,
                endTime,
                cityCode,
                selectedUserId
            ).then((res: any) => {
                if (res.success) {
                    message.success(res.msg)
                    setTimeout(() => {
                        window.location.reload()
                    }, 500);
                } else {
                    message.error(res.msg)
                }
            })
        }
    }

    const hasSelected = selectedRowKeys.length > 0;

    return (
        <div>
            <div>
                <Breadcrumb style={{ textAlign: "left", marginBottom: 25 }}>
                    <Breadcrumb.Item>推荐</Breadcrumb.Item>
                    <Breadcrumb.Item>
                        <a href="">推荐列表</a>
                    </Breadcrumb.Item>
                </Breadcrumb>
                <div style={{ marginBottom: 16, textAlign: "left" }}>
                    <Button type="primary" onClick={addRecommend} style={{ marginRight: 20 }}>
                        添加
                    </Button>
                    <Button type="danger" disabled={!hasSelected} onClick={() => {
                        deleteRecommend()
                    }}>
                        删除
                    </Button>

                    <span style={{ marginLeft: 8 }}>
                        {hasSelected ? `选择了${selectedRowKeys.length}项` : ''}
                    </span>
                </div>
                <Table columns={columns} dataSource={tableData}
                    rowSelection={rowSelection}
                    pagination={{ pageSize: 10, "total": total, defaultCurrent: currentPage }}
                    onChange={handleChange}
                    locale={{ filterConfirm: "确定", filterReset: "重置", emptyText: "暂无数据" }} />
                <Drawer
                    // width={500}
                    // title="添加广告"
                    placement="top"
                    closable={true}
                    onClose={() => {
                        setDrawerShow(false)
                    }}
                    visible={drawerShow}
                    height="auto"
                >
                    <div style={{
                        display: "flex",
                        flexWrap: "wrap"
                    }}>
                        1
                </div>
                </Drawer>
                {
                    show ?
                        <AdvertisementDetail
                            show={show}
                            relateId={relateId}
                            relateType={relateType}
                            hide={() => setShow(false)}
                        />
                        : ""
                }
                <Drawer
                    width={500}
                    title={edit?"编辑推荐":"添加推荐"}
                    placement="right"
                    closable={true}
                    onClose={() => {
                        clearData()
                    }}
                    visible={recommendAddDrawerShow}
                >
                    <RadioGroup onChange={(e: any) => { setRecommendType(e.target.value) }} value={recommendType}>
                        <Radio value={1}>推荐</Radio>
                        <Radio value={2}>优选活动</Radio>
                    </RadioGroup>
                    <div style={{ margin: "20px 0" }}>
                        <RadioGroup onChange={(e: any) => { setRecommendRelateType(e.target.value) }} value={recommendRelateType}>
                            <Radio value={1}>宠物</Radio>
                            <Radio value={2}>商品</Radio>
                            <Radio value={3}>服务</Radio>
                        </RadioGroup>
                    </div>
                    <div style={{ margin: "20px 0" }}>
                        <span>名称：</span>
                        <Input style={{ width: "70%" }} placeholder="请输入名称"
                            value={name}
                            onChange={(e: any) => setName(e.target.value)} />
                    </div>
                    <div style={{ margin: "20px 0" }}>
                        <span>用户：</span>
                        <Input style={{ width: "70%" }} placeholder="请输入用户"
                            value={selectedUserId}
                            onClick={() => setUserTableShow(true)} />
                    </div>
                    <div style={{ margin: "20px 0" }}>
                        <span>关联：</span>
                        <Input style={{ width: "70%" }} placeholder="请输入关联表id"
                            value={selectedRelateId}
                            onClick={() => setUserRelateTableShow(true)} />
                    </div>
                    <div style={{ margin: "20px 0" }}>
                        <span>时间：</span>
                        <RangePicker locale={locale} 
                        value={[
                        moment(moment(parseInt(startTime,10)).format('YYYY-MM-DD'),'YYYY-MM-DD'),
                        moment(moment(parseInt(endTime,10)).format('YYYY-MM-DD'),'YYYY-MM-DD')]}
                        onChange={onChange} format="YYYY-MM-DD" />
                    </div>
                    <div style={{ margin: "20px 0" }}>
                        <span>城市：</span>
                        <RadioGroup onChange={(e: any) => { setCityCode(e.target.value) }} value={cityCode}>
                            <Radio.Button value="321000">扬州</Radio.Button>
                            <Radio.Button value="340300">蚌埠</Radio.Button>
                        </RadioGroup>
                    </div>
                    <div
                        style={{
                            position: 'absolute',
                            left: 0,
                            bottom: 0,
                            width: '100%',
                            borderTop: '1px solid #e9e9e9',
                            padding: '10px 16px',
                            background: '#fff',
                            textAlign: 'right',
                        }}
                    >
                        <Button onClick={() => setDrawerShow(false)} style={{ marginRight: 8 }}>
                            取消
                            </Button>
                        <Button onClick={() => {
                            // this.setState({ visible: true })
                            // this.submitPetType()
                            // setDrawerShow(false)
                            if (edit) {
                                submitAddRecommend(2, 1)
                            } else {
                                submitAddRecommend(1, 1)
                            }
                        }}
                            type="primary">
                            确定
                            </Button>
                    </div>
                </Drawer>
                <Modal 
                    width={1200}
                    visible={userTableShow}
                    onCancel={() => setUserTableShow(false)}
                    onOk={()=>setUserTableShow(false)}
                    okText="确定"
                    cancelText="取消"
                >
                    <UserList selectUserCallback={(id:string)=>{
                        setSelectedUserId(id)
                    }}/>
                </Modal>
                <Modal 
                    width={500}
                    visible={userRelateTableShow}
                    onCancel={() => setUserRelateTableShow(false)}
                    onOk={()=>setUserRelateTableShow(false)}
                    okText="确定"
                    cancelText="取消"
                >
                    <RelateContent type={recommendRelateType}
                    userId={selectedUserId}
                    selectRelateIdCallback={(id:string)=>{
                        console.log(id)
                        setSelectedRelateId(id)
                    }}
                    />
                </Modal>
            </div >
        </div >
    )
}

export default Recommend