import * as React from 'react';
import { Tag } from "antd";

interface IRecommendRelateTypeBoxProps {
    type: number
}

export class RecommendRelateTypeBox extends React.Component<IRecommendRelateTypeBoxProps> {

    public render() {
        const Boxes = [{
            color: "#673ab7",
            content: "宠物"
        }, {
            color: "#ff5516",
            content: "商品"
        }, {
            color: "#2196f3",
            content: "服务"
        },]
        return (
            <div>
                <Tag color={Boxes[this.props.type].color}>{Boxes[this.props.type].content}</Tag>
            </div>
        );
    }
}
